"use client";

import React, { useState } from "react";
import { useGameState } from "@/lib/gameState";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { type Item } from "@/lib/items";

export default function InventoryPanel() {
  const { inventory, removeItem } = useGameState();
  const [selectedItem, setSelectedItem] = useState<{ item: Item; index: number } | null>(null);

  const getRarityColor = (rarity?: string) => {
    switch (rarity) {
      case 'magic': return 'bg-blue-500';
      case 'rare': return 'bg-yellow-500';
      case 'unique': return 'bg-orange-500';
      default: return 'bg-gray-500';
    }
  };

  const getRarityTextColor = (rarity?: string) => {
    switch (rarity) {
      case 'magic': return 'text-blue-400';
      case 'rare': return 'text-yellow-400';
      case 'unique': return 'text-orange-400';
      default: return 'text-gray-400';
    }
  };

  return (
    <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
      {/* Inventory Grid */}
      <div className="lg:col-span-2">
        <Card>
          <CardHeader>
            <CardTitle>Inventory ({inventory.length} items)</CardTitle>
            <CardDescription>
              Click on an item to view details or manage it
            </CardDescription>
          </CardHeader>
          <CardContent>
            {inventory.length > 0 ? (
              <div className="grid grid-cols-2 md:grid-cols-4 gap-3">
                {inventory.map((item, idx) => (
                  <div
                    key={idx}
                    className={`p-4 border-2 rounded-lg text-center transition-all cursor-pointer hover:shadow-md ${
                      selectedItem?.index === idx 
                        ? 'border-primary bg-primary/10' 
                        : 'border-border hover:border-primary/50'
                    }`}
                    onClick={() => setSelectedItem({ item, index: idx })}
                  >
                    <div className={`font-bold text-sm mb-1 ${getRarityTextColor(item.rarity)}`}>
                      {item.name}
                    </div>
                    <div className="space-y-1 text-xs">
                      {item.damage && (
                        <div className="text-red-400">DMG: {item.damage}</div>
                      )}
                      {item.defense && (
                        <div className="text-blue-400">DEF: {item.defense}</div>
                      )}
                      {item.rarity && (
                        <Badge 
                          variant="secondary" 
                          className={`${getRarityColor(item.rarity)} text-white text-xs`}
                        >
                          {item.rarity}
                        </Badge>
                      )}
                    </div>
                  </div>
                ))}
              </div>
            ) : (
              <div className="text-center py-12 text-muted-foreground">
                <p className="text-lg mb-2">Your inventory is empty</p>
                <p className="text-sm">Defeat enemies and explore to find items</p>
              </div>
            )}
          </CardContent>
        </Card>
      </div>

      {/* Item Details Panel */}
      <div className="lg:col-span-1">
        <Card className="sticky top-4">
          <CardHeader>
            <CardTitle>Item Details</CardTitle>
          </CardHeader>
          <CardContent>
            {selectedItem ? (
              <div className="space-y-4">
                <div>
                  <h3 className={`text-lg font-bold ${getRarityTextColor(selectedItem.item.rarity)}`}>
                    {selectedItem.item.name}
                  </h3>
                  {selectedItem.item.rarity && (
                    <Badge 
                      variant="secondary" 
                      className={`${getRarityColor(selectedItem.item.rarity)} text-white mt-1`}
                    >
                      {selectedItem.item.rarity}
                    </Badge>
                  )}
                </div>

                <div className="space-y-2">
                  {selectedItem.item.damage && (
                    <div className="flex justify-between">
                      <span>Damage:</span>
                      <span className="text-red-400 font-bold">{selectedItem.item.damage}</span>
                    </div>
                  )}
                  {selectedItem.item.defense && (
                    <div className="flex justify-between">
                      <span>Defense:</span>
                      <span className="text-blue-400 font-bold">{selectedItem.item.defense}</span>
                    </div>
                  )}
                </div>

                <div className="border-t pt-4">
                  <p className="text-sm text-muted-foreground mb-4">
                    {selectedItem.item.description}
                  </p>
                </div>

                <div className="space-y-2">
                  <Button 
                    variant="destructive" 
                    size="sm" 
                    className="w-full"
                    onClick={() => {
                      removeItem(selectedItem.index);
                      setSelectedItem(null);
                    }}
                  >
                    Drop Item
                  </Button>
                </div>
              </div>
            ) : (
              <div className="text-center py-8 text-muted-foreground">
                <p>Select an item to view details</p>
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
