"use client";

import React from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { useGameState } from "@/lib/gameState";
import { Button } from "@/components/ui/button";
import { cn } from "@/lib/utils";

export default function GameNav() {
  const pathname = usePathname();
  const { character, health, mana } = useGameState();

  if (!character) {
    return null;
  }

  const navItems = [
    { href: "/map", label: "Map" },
    { href: "/inventory", label: "Inventory" },
    { href: "/battle", label: "Battle" },
  ];

  return (
    <div className="mb-8">
      {/* Character Status Bar */}
      <div className="bg-card border rounded-lg p-4 mb-4">
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-6">
            <div>
              <h3 className="font-bold text-lg">{character.name}</h3>
              <p className="text-sm text-muted-foreground">Level 1</p>
            </div>
            <div className="flex space-x-4">
              <div>
                <div className="text-sm font-medium">Health</div>
                <div className="w-32 bg-secondary rounded-full h-3">
                  <div 
                    className="bg-red-500 h-3 rounded-full transition-all duration-300"
                    style={{ width: `${(health / character.baseHealth) * 100}%` }}
                  />
                </div>
                <div className="text-xs text-center">{health}/{character.baseHealth}</div>
              </div>
              <div>
                <div className="text-sm font-medium">Mana</div>
                <div className="w-32 bg-secondary rounded-full h-3">
                  <div 
                    className="bg-blue-500 h-3 rounded-full transition-all duration-300"
                    style={{ width: `${(mana / character.baseMana) * 100}%` }}
                  />
                </div>
                <div className="text-xs text-center">{mana}/{character.baseMana}</div>
              </div>
            </div>
          </div>
          <Link href="/">
            <Button variant="outline" size="sm">
              New Character
            </Button>
          </Link>
        </div>
      </div>

      {/* Navigation Menu */}
      <nav className="flex justify-center space-x-2">
        {navItems.map((item) => (
          <Link key={item.href} href={item.href}>
            <Button
              variant={pathname === item.href ? "default" : "outline"}
              className={cn(
                "transition-all",
                pathname === item.href && "bg-primary text-primary-foreground"
              )}
            >
              {item.label}
            </Button>
          </Link>
        ))}
      </nav>
    </div>
  );
}
